"use client";

import { useState, useEffect } from "react";
import DashboardLayout from "@/components/dashboard-layout";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Progress } from "@/components/ui/progress";
import { Badge } from "@/components/ui/badge";

// Mock data
const mockStats = {
  totalProjects: 12,
  activeProjects: 8,
  completedTasks: 145,
  pendingTasks: 23,
  teamMembers: 6,
  hoursTracked: 320
};

const mockRecentTasks = [
  {
    id: 1,
    title: "Design new landing page",
    project: "Website Redesign",
    status: "In Progress",
    priority: "High",
    dueDate: "2024-01-15"
  },
  {
    id: 2,
    title: "Fix authentication bug",
    project: "Mobile App",
    status: "Review",
    priority: "Critical",
    dueDate: "2024-01-12"
  },
  {
    id: 3,
    title: "Update user documentation",
    project: "Documentation",
    status: "Todo",
    priority: "Medium",
    dueDate: "2024-01-20"
  }
];

const mockProjects = [
  {
    id: 1,
    name: "Website Redesign",
    progress: 75,
    tasksCompleted: 12,
    totalTasks: 16,
    status: "On Track"
  },
  {
    id: 2,
    name: "Mobile App",
    progress: 45,
    tasksCompleted: 9,
    totalTasks: 20,
    status: "At Risk"
  },
  {
    id: 3,
    name: "Marketing Campaign",
    progress: 90,
    tasksCompleted: 18,
    totalTasks: 20,
    status: "On Track"
  }
];

export default function Dashboard() {
  const [stats, setStats] = useState(mockStats);
  const [recentTasks, setRecentTasks] = useState(mockRecentTasks);
  const [projects, setProjects] = useState(mockProjects);

  const getPriorityColor = (priority: string) => {
    switch (priority.toLowerCase()) {
      case "critical":
        return "destructive";
      case "high":
        return "destructive";
      case "medium":
        return "default";
      case "low":
        return "secondary";
      default:
        return "default";
    }
  };

  const getStatusColor = (status: string) => {
    switch (status.toLowerCase()) {
      case "completed":
        return "default";
      case "in progress":
        return "default";
      case "review":
        return "secondary";
      case "todo":
        return "outline";
      default:
        return "outline";
    }
  };

  const getProjectStatusColor = (status: string) => {
    switch (status.toLowerCase()) {
      case "on track":
        return "text-green-600 bg-green-100 dark:text-green-400 dark:bg-green-900/20";
      case "at risk":
        return "text-yellow-600 bg-yellow-100 dark:text-yellow-400 dark:bg-yellow-900/20";
      case "delayed":
        return "text-red-600 bg-red-100 dark:text-red-400 dark:bg-red-900/20";
      default:
        return "text-gray-600 bg-gray-100 dark:text-gray-400 dark:bg-gray-900/20";
    }
  };

  return (
    <DashboardLayout>
      <div className="space-y-6">
        {/* Welcome Section */}
        <div>
          <h2 className="text-2xl font-bold text-gray-900 dark:text-white">
            Welcome back!
          </h2>
          <p className="text-gray-600 dark:text-gray-400">
            Here's what's happening with your projects today.
          </p>
        </div>

        {/* Stats Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-gray-600 dark:text-gray-400">
                Total Projects
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{stats.totalProjects}</div>
              <p className="text-xs text-green-600 dark:text-green-400">
                +2 from last month
              </p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-gray-600 dark:text-gray-400">
                Active Projects
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{stats.activeProjects}</div>
              <p className="text-xs text-blue-600 dark:text-blue-400">
                {stats.activeProjects} in progress
              </p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-gray-600 dark:text-gray-400">
                Completed Tasks
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{stats.completedTasks}</div>
              <p className="text-xs text-green-600 dark:text-green-400">
                +12 this week
              </p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-gray-600 dark:text-gray-400">
                Pending Tasks
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{stats.pendingTasks}</div>
              <p className="text-xs text-orange-600 dark:text-orange-400">
                {stats.pendingTasks} need attention
              </p>
            </CardContent>
          </Card>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          {/* Recent Tasks */}
          <Card>
            <CardHeader>
              <CardTitle>Recent Tasks</CardTitle>
              <CardDescription>
                Your latest task updates
              </CardDescription>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                {recentTasks.map((task) => (
                  <div key={task.id} className="flex items-center justify-between p-3 border rounded-lg">
                    <div className="flex-1">
                      <h4 className="font-medium text-sm">{task.title}</h4>
                      <p className="text-xs text-gray-600 dark:text-gray-400">{task.project}</p>
                      <p className="text-xs text-gray-500 dark:text-gray-500">Due: {task.dueDate}</p>
                    </div>
                    <div className="flex flex-col gap-1">
                      <Badge variant={getStatusColor(task.status)} className="text-xs">
                        {task.status}
                      </Badge>
                      <Badge variant={getPriorityColor(task.priority)} className="text-xs">
                        {task.priority}
                      </Badge>
                    </div>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>

          {/* Project Progress */}
          <Card>
            <CardHeader>
              <CardTitle>Project Progress</CardTitle>
              <CardDescription>
                Track your project completion
              </CardDescription>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                {projects.map((project) => (
                  <div key={project.id} className="space-y-2">
                    <div className="flex items-center justify-between">
                      <h4 className="font-medium text-sm">{project.name}</h4>
                      <span className={`text-xs px-2 py-1 rounded-full ${getProjectStatusColor(project.status)}`}>
                        {project.status}
                      </span>
                    </div>
                    <div className="flex items-center justify-between text-xs text-gray-600 dark:text-gray-400">
                      <span>{project.tasksCompleted}/{project.totalTasks} tasks</span>
                      <span>{project.progress}%</span>
                    </div>
                    <Progress value={project.progress} className="h-2" />
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>
        </div>
      </div>
    </DashboardLayout>
  );
}
